.main-menu-cont {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  padding-bottom: 2em;
  @apply flex flex-col items-center justify-center bg-red-50;
}
.main-menu-title {
  @apply mt-2 p-4 text-5xl font-bold;
}
.main-menu-cont h1 {
  font-family: Stalemate;
}
.soir-title {
  @apply p-2 text-center text-2xl;
}
.item p {
  font-size: small;
  padding: 0.5em;
}
.raclette-main-cont {
  @apply w-full items-center justify-center;
}
.raclette-cont {
  @apply flex w-11/12 flex-row items-center justify-between pl-4;
}
.raclette-main-cont h4 {
  padding: 1em;
  padding-bottom: 0.5em;
  text-decoration: underline;
}
.menu-soir {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding-bottom: 2em;
  box-sizing: content-box;
  @apply w-11/12 items-center justify-center border-solid border-[var(--accent-color)] bg-red-200 sm:w-96 sm:border-2 sm:p-8;
}
