:root {
  /* --main-bg-color: #9d2d3e; */
  --main-bg-color: #f8ebed;
  --accent-color: #a1162b;
  --pic-height: 427px;
}
html,
body {
  width: 100%;
  overflow-x: hidden;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.info-cont {
  @apply absolute top-[var(--pic-height)] z-10 w-full bg-red-50 sm:top-[650px];
}
.head-cont {
  @apply absolute top-0 z-10 w-full sm:z-10 sm:flex sm:flex-col;
}
.map-cont {
  @apply flex w-full flex-col-reverse bg-red-50 sm:flex-row;
}
@media screen and (min-width: 500px) {
  .header-cont {
    z-index: 9;
  }
}
