.hor-cont {
  border-top: none;
  border-bottom: 5px solid var(--main-bg-color);
  @apply w-full bg-red-50 py-3 text-center;
}
.horaires.hidden {
  width: 0%;
  height: 0%;
  overflow: hidden;
  border: none;
  display: none;
}
.horaires.show-horaires {
  width: 100%;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
